.latest-posts-wrapper {
    display: flex;
    align-items: flex-start;
    gap: 1%;
    width: 100%;
    background-color: #fff;
    padding: 10px;
}
.single-post {
    width: 32%;
    min-height: 480px;
    border: 1px solid #ddd;
    padding: 10px;
}
.single-post img {
    width: 100%;
    height: 200px;
    margin-bottom: 5px;
}
.single-post h2 {
    font-size: 24px;
    margin-top: 0;
    margin-bottom: 10px;
}
.single-post .meta-data {
    display: flex;
    align-items: center;
    gap: 10px;
}
.single-post .meta-data span {
    font-size: 14px;
}
.single-post p {
    font-size: 15px;
    color: #222;
}
